.Statusbar_container{
    height: 120px;
    width: 615px;
    background-color: white;
    border: 1px solid #dbdbdb;
    margin-top: 25px;
    display: flex;
    overflow-x: scroll;
}
.Statusbar_container::-webkit-scrollbar{
    display: none;
}
.statusbar_status{
    min-height: 55px;
    min-width: 55px;
    border: 3px solid #df3968;
}
.status{
    margin: 25px 15px;
}
.statusbar_text{
    font-size: 12px;
    max-width: 60px;
    overflow-x: hidden;
    text-align: center;
}